.App-Links {
  text-align: center;
  display: -ms-flexbox;
  display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-pack: center;
	    justify-content: center;
	height: 100vh;
	width: 100vw;
  background-color: #222;
	-ms-flex-line-pack: center;
	    align-content: center;
