.App-intro {
  font-size: large;
}

boxText {
	margin-left: 50px;
}


/* expanded/linkBox stuff css */
.expandedBarCol {
  -ms-flex-line-pack: center;
      align-content: center;
  display: -ms-flexbox;
  display: flex;
}

.expandedBarRow {
  padding-top: 20px;
}

/* resume stuff */
.resume-row {
  display: -ms-flexbox;
  display: flex;
  text-align: left;
  -ms-flex-line-pack: center;
      align-content: center;
}

dl {
  margin-bottom: $spacer;
	margin-right: auto;
	margin-left: auto;
}

dt, dd {
  display: inline-block;
  vertical-align: top;
  margin-bottom: $spacer;
}

@media(min-width: 640px) {
  dt, dd {
    margin-bottom: 60px;
  }

  .experience {
    dt {
      width: 30%;
      padding-right: ($spacer * 2);
  }
    dd {
      width: 70%;
    }

  }

  .education {
    dt, dd {
      width: 50%;
    }
  }
}


@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}


@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



